import React from "react";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import { BrandContext } from "../../BrandContext";
import themes from "../../Theme/themes";
import ThemeProviderCustom from "../../ThemeProviderCustom";
import { BrandType } from "../../types/brand";
import mockBrands from "../../utils/brandMock";
import Logo from "../Logo";

const useStyles = makeStyles((theme) => ({
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type LogoStoryProps = {
    mode: string,
    brand: BrandType,
    type: string
}

const LogoStory = ({
    mode, brand, type
}: LogoStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <BrandContext.Provider value={mockBrands[brand]}>
            <ThemeProviderCustom>
                <Paper className={classes[mode]} id="Logo">
                    <Typography variant="h3">Logo</Typography>
                    <br />
                    <Divider />
                    <br />
                    <Grid>
                        <Logo type={type} alt="Logo" className={{}} />
                    </Grid>
                </Paper>
            </ThemeProviderCustom>
        </BrandContext.Provider> 
    );
};

const argsControls = {
    args: {
        brand: "sub",
        mode: "root",
        type: "dark",
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["cvc", "cvccorp", "sub"]
        },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        },
        type: { 
            type: "select",
            options: ["dark", "light"]
        }
    }
}

storiesOf("Components/Logo", module)
    .add("Default", (args) => <LogoStory {...args}/>, argsControls);
